export default {
	props:{
		minValue:{
			type:Number,
			default:1
		},
		maxValue:{
			type:Number,
			default:999
		},
		
		
	},
	data() {
		return {
			inputValue:1
		}

	},
	watch:{
		inputValue(newVal,oldVal){
			// console.log(newVal);
			if(newVal < this.minValue){
				this.inputValue = this.minValue
			}
			if(newVal > this.maxValue){
				this.inputValue = this.maxValue
			}
		}
	},
	methods:{
		clickItem(e){
			if(e === 1){
				this.inputValue --;
				
			}else{
				this.inputValue ++;
				
			}
			
			this.$emit('inputItem',this.inputValue)
		}
	}
}
